/* HTML ICON */

.html-icon path {
    fill: transparent;
    stroke-width: 0.18;
    stroke: white;
    stroke-dasharray: 35;
    stroke-dashoffset: 0;
    animation: animate-html 2s linear 4s;
}

/* .html-icon:hover path {
  animation: animate-html 2s linear forwards;
} */

@keyframes animate-html {
    0% {
        stroke-dashoffset: 0;
    }

    40% {
        stroke-dashoffset: 35;
    }

    80% {
        stroke-dashoffset: 70;

    }

    100% {
        stroke-dashoffset: 70;

    }
}


