/* Tilt Effect from Vanilla Tilt */


.project-card {
    display: grid;
    grid-template-columns: 300px;
    grid-template-rows: 210px 150px 80px;
    grid-template-areas: "image""text""stats";
    border-radius: 18px;
    background: #1d1d1d;
    color: white;
    box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.9);
    text-align: justify;
    cursor: pointer;
    margin: 30px;
    transform-style: preserve-3d;
    transform: perspective(1000px);
}

.rgb::after {
    content: "";
    background: linear-gradient(45deg,
            #00F0FF 0%,
            #6CF7B0 10%,
            #00F0FF 20%,
            #ba0cf8 30%,
            #00F0FF 40%,
            #7AE286 50%,
            #00F0FF 60%,
            #6CF7B0 70%,
            #ba0cf8 80%,
            #6CF7B0 90%,
            #00F0FF 100%) repeat 0% 0% / 300% 100%;
    position: absolute;
    inset: -3px;
    border-radius: 16px;
    filter: blur(8px);
    transform: translateZ(-1px);
    /*or z-index */
    animation: rgb 6s linear infinite;
}

@keyframes rgb {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}

