/* Animated Arrow  https://codepen.io/bitstarr/pen/XjaJGz*/
.arrow {
    position: relative;
    bottom: 7.5rem;
    left: 3%;
    margin-left: -20px;
    width: 60px;
    height: 60px;

    /**
   * Dark Arrow Down
   */

    background-image: url(data:image/svg+xml;base64,);
    background-size: contain;

}

.bounce {
    animation: bounce 2s infinite;
}

@keyframes bounce {

    0%,
    20%,
    50%,
    80%,
    100% {
        transform: translateX(0);
    }

    40% {
        transform: translateX(-30px);
    }

    60% {
        transform: translateX(-15px);
    }
}

